code {
  font-family: 'Space Mono', source-code-pro, Menlo, Monaco, Consolas,
    'Courier New', monospace;
}

.container {
  max-width: 980px;
  margin: 0 auto;
}

.hover-success:hover {
  color: #209cee;
}
.hover-error:hover {
  color: #ce372b;
}

.nes-list.is-disc li::before {
  color: inherit;
}

.fixed-width {
  width: 100px;
}

.table-fixed {
  table-layout: fixed;
}

.table-fixed td,
.table-fixed th {
  min-width: 108px;
  width: 108px;
  padding: 0 0.25rem;
  border-right: 1px solid #e0e0e0;
}

.table-fixed tbody th {
  min-width: 236px;
}

